* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

.nav {
  display: flex;
  justify-content: space-between;
  padding-right: 1rem;
  padding-left: 1rem;
  align-items: center;
  background-color: #1E201E;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 100;
}

.links {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.nav a {
  color: #E9EFEC;
  text-decoration: none;
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 1.5rem;
  font-weight: bolder;
}

.nav a:hover {
  color: rgb(211, 69, 18);
}

.nav a.active {
  color: red;
  border-bottom: 5px solid orangered;
}

.container {
  /* margin-top: 1rem; */
  text-align: center;
}

@media (max-width: 768px) {
  .nav {
    flex-direction: column;
    align-items: flex-start;
  }

  .links {
    margin-top: 1rem;
  }

  .links li {
    margin: 0.5rem 0;
  }
}

.active {
  color: blue;
  /* border-bottom: 2px solid red; */
}
#home {
    display: flex;
    flex-direction: column;
    margin-top: 3%;
    margin-right: 15%;
    justify-content: center;
    color: black;
}

#name {
    font-size: 200%;
    width: 105%;
    font-family: monospace;
    position: relative;
    -webkit-animation-name: example;
            animation-name: example;
    -webkit-animation-duration: 4s;
            animation-duration: 4s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    color: orangered;
}

@-webkit-keyframes example {
    0% {
        color: white;
        left: 0px;
        top: 0px;
    }

    25% {
        left: 100px;
        top: 0px;
    }

}

@keyframes example {
    0% {
        color: white;
        left: 0px;
        top: 0px;
    }

    25% {
        left: 100px;
        top: 0px;
    }

}

.main {
    display: flex;
    flex-direction: column;
    text-align: left;
} 

.headings {
    font-family: century;
    font-size: 2rem;
    text-align: left;
    margin-left: 5%;
}

#content_me {
    font-size: smaller;
    width: 95%;
    font-weight: 1;
    -webkit-animation-duration: 5s;
            animation-duration: 5s;
    font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
} 

.resume {
    display: flex;
    border: 2px solid orangered;
    border-radius: 10px;
    color: orangered;
    background-color: white;
    font-size: 15px;
    justify-content: center;
    align-items: center;
    width: 110%;
    padding: 10px;
}

.resume:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background-color: orangered;
    color: whitesmoke;
    border: 2px solid orangered;
}

a {
    text-decoration: none;
}

.sujeeth_img {
    margin: auto;
    display: flex;
    border-radius: 50%;
    height: 450px;
    border: 2px solid transparent;
    float: right;
}

.contents {
    /* margin: 0;
    margin-top: 1%; */
    display: flex;
    justify-content: center;
    align-items: center;
}

.contents div {
    display: flex;
    flex: 1 1;
    align-items: center;
}


/* For screens larger than 768px */
@media (min-width: 768px) {
  .contents {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 50px;
  }

  .right-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .left-content {
    flex: 2 1;
    margin-right: 50px;
  }

  #name {
    font-size: 4rem;
  }

  #content_me {
    font-size: 1.5rem;
  }

  .resume-link {
    margin-top: 20px;
  }
}

/* For screens smaller than 768px */
@media (max-width: 768px) {
  .contents {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px;
  }

  .right-content {
    margin-bottom: 30px;
  }

  #name {
    font-size: 3rem;
  }

  #content_me {
    font-size: 1.2rem;
    text-align: center;
  }

  .resume-link {
    margin-top: 20px;
  }
}


/* Basic styling for the resume */

body {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #333;
  margin: 0;
  padding: 0;
}

/* Styling for specific elements */

h1 {
  font-size: xx-large;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  color: orangered;
  justify-content: center;
  align-items: center;
}

.resume_card-container {
  width: 500px;
  height: 100%;
  overflow: hidden;
  box-shadow: 0px 0px 15px -5px;
  border-radius: 5px;
  background-color: azure;
  justify-content: center;
  padding: 20px;
}

.resume_card-container img {
  overflow: hidden;
  height: 200px;
}

.resume_card-container:hover {
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
  box-shadow: 0px 0px 15px 0px;
}

.resume_maindiv {
  display: flex;
  justify-content: center;
  grid-gap: 5rem;
  gap: 5rem;
  flex-wrap: wrap;
  margin-bottom: 5%;
}

.resume_card-title {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-gap: 7rem;
  gap: 7rem;
  flex-wrap: wrap;
  text-align: justify;
}

.description {
  justify-content:space-between;
  align-content:stretch;
  text-align: justify;
}

.resume_icons {
  font-size: 50px;
  margin-right: 20px;
}

.resume_icons:last-of-type {
  margin-right: 0;
}
.Slider {
  background-color: rgb(250, 249, 246);
  max-height: 600px;
  max-width: 700px;
  overflow: hidden;
  margin-left: auto;
  margin-right: auto;
  justify-content: center;
  align-items: center;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 20px;
}

.project-image img {
  margin-top: 20px;
  width: 250px;
  margin-left: auto;
  margin-right: auto;
  border-radius: 10px;
}

.implement {
  display: flex;
}

.implement img{
  margin-top: 20px;
  width: 100px;
  height: 100px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
}
.skill-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    grid-column-gap: 3rem;
    -webkit-column-gap: 3rem;
            column-gap: 3rem;
}

.skill-list {
    display: flex;
    justify-content: center;
    align-items: center;
    /* flex: 5; */
}

.skill h1 {
    font-size: xx-large;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: orangered;
    justify-content: center;
    align-items: center;
}

.card-container {
    width: 200px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    border-radius: 5px;
    background-color: azure;
} 

.card-container:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    /* box-shadow: 0px 0px 15px 0px; */
}

.image-container img{
    margin-top: 5%;
    overflow: hidden;
    height: 100px;
}

.card-title {
    font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
    font-weight: 1;
} 

.frontend, .backend {
    font-size: 150%;
    font-weight: 500;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.maindiv {
    display: flex;
    justify-content: center;
    grid-gap: 1.5rem;
    gap: 1.5rem;
    flex-wrap: wrap;
    /* padding-bottom: 3%; */
}

body {
    background-color: #D2E0FB;
}
